import {
  Apis
} from 'bitsharesjs-ws';
import EventEmitter from 'events';

class BitShares extends EventEmitter {


  constructor(node) {
    super();
    this.node = node;
  }
  async connect() {

    this.subscription = await Apis.instance(this.node, true).init_promise.then((res) => {
      console.log('connected to:', res[0].network);
      this.api = Apis.instance();
      let self = this;
      this.api.db_api().exec('set_subscribe_callback', [function (obj) {
        self.updateListener(obj, self);
      }, false]);
      this.api.db_api().exec("get_objects", [ ["2.1.0"] ] );
    });
    return this.subscription;
  }
  async getBlock(blockno) {

    return this.api.db_api().exec('get_block', [blockno]);

  }
  async updateListener(object, self) {
    self.emit('block', object);
  }
}
export default BitShares;
