<template>
    <div class="stats">
        <div class="top">

            <div class="logo">
                <img src="img/logo.png"/>
            </div>
            <div class="numbers">
                <table>
                    <tr>
                        <td align="right">
                            <label>Block No:</label>
                        </td>
                        <td align="left">
                            <span class="changed" id="blockNumber">{{ blockNo }}</span>
                        </td>
                    </tr>
                    <tr>
                        <td align="right">
                            <label>Txs in block:</label>
                        </td>
                        <td align="left">
                            <span class="changed" id="txNumber">{{ txs }}</span>
                        </td>
                    </tr>
                    <tr>
                        <td align="right">
                            <label>Ops in block:</label>
                        </td>
                        <td align="left">
                            <span class="changed" id="opNumber">{{ ops }}</span>
                        </td>
                    </tr>
                    <tr>
                        <td align="right">
                            <label>Txs/s (24h):</label>
                        </td>
                        <td align="left">
                            <span class="changed" id="tx24Number">{{ txs24 }}</span> ({{ txs24total }} total)
                        </td>
                    </tr>
                    <tr>
                        <td align="right">
                            <label>Ops/s (24h):</label>
                        </td>
                        <td align="left">
                            <span class="changed" id="op24Number">{{ ops24 }}</span> ({{ ops24total }} total)
                        </td>
                    </tr>
                    <tr>
                        <td align="right">
                            <label>Unique Users (24h):</label>
                        </td>
                        <td align="left">
                            <span class="changed" id="uniqueNumber">{{ unique }}</span>
                        </td>
                    </tr>
                </table>
            </div>
        </div>
        <table @click="toggleUpdates" class="operationList">
            <tr
                v-for="operation in reverseOps"
                :key="operation[2]"
            >
                <td :style="'text-align:center; width:170px;font-size:12px; font-weight:bold;color:white;background: #'+operationReadable(operation[0]).color">{{ operationReadable(operation[0]).name }}</td><td><code>{{ operationReadableContent(operation[1]) }}</code></td>
            </tr>
        </table>
    </div>
</template>

<script>
    import BitShares from "../lib/BitShares";
    import Queue from "../lib/Queue";
    import elasticsearch from "elasticsearch";

    export default {
        name: "Explorer",
        props: {
            es: {
                type: String,
                default: "https://BitShares:Infrastructure@eu.elasticsearch.bitshares.ws"
            },
            node: {
                type: String,
                default: "wss://bts-seoul.clockwork.gr"
            },
        },
        data() {
            return {
                bitshares: {},
                queue: null,
                blockNo: "",
                txs: 0,
                ops: 0,
                txs24: 0,
                ops24: 0,
                txs24total: 0,
                ops24total: 0,
                unique: 0,
                client: null,
                operations: [],
                toadd: [],
                interval: null,
                stopped:false,
                displayops:[]
            };
        },
        computed: {
            reverseOps() {
                return this.displayops.slice().reverse();
            }
        },
        mounted() {
            console.log("[Node] Instance: ", this.node);
            this.bitshares = new BitShares(this.node);

            // asian nodes
            //   wss://bts-seoul.clockwork.gr
            //   wss://master.eu.api.bitshares.org/ws
            //   wss://ap-southeast-3.bts.crypto-bridge.org
            //   wss://ap-southeast-1.bts.crypto-bridge.org
            //   wss://singapore.asia.api.bitshares.org/ws
            //   wss://ws.gdex.top

            this.bitshares.connect();
            console.log("[Node] Connected");

            this.queue = new Queue();

            console.log("[ElasticSearch] Instance: ", this.es);
            this.client = new elasticsearch.Client({
                host: this.es
            });
            console.log("[ElasticSearch] Connected");

            // asian ES
            // bts-es.clockwork.gr:5005
            // https://BitShares:Infrastructure@elasticsearch.bitshares.ws

            let blockFromNode = false;


            this.bitshares.on("block", async block => {
                this.queue.enqueue(
                    this.getDataFromES(block[0][0].head_block_number-2).catch(err => {
                        console.error("failed", err);
                    })
                );
                console.log("[Node] Received block: ", block[0][0].head_block_number);//, block[0][0]);
                // visualize block
                if (blockFromNode) {
                    this.bitshares.getBlock(block[0][0].head_block_number - 2).then(blockData => {
                        this.blockNo = block[0][0].head_block_number - 2;
                        this.txs = blockData.transactions.length;
                        let _ops = 0;
                        blockData.transactions.forEach(_tx => {
                            _ops += _tx.operations.length;
                        });
                        this.ops = _ops;
                    });
                }

                if (this.queue.head().PromiseStatus != "pending") {
                    let details = await this.queue.dequeue();
                    if (!details) {
                        return;
                    }
                    console.log("[ElasticSearch] Received block: ", block[0][0].head_block_number-2);//, details);
                    if (!blockFromNode) {
                        let _ops = details.responses[0].hits.total;
                        if (_ops.value) {
                            _ops = _ops.value;
                        }
                        this.ops = _ops;
                    }
                    let trxs=new Set();
                    let newtoadd=[];
                    for (var i = 0; i < details.responses[0].hits.hits.length; i++) {
                        let _item = details.responses[0].hits.hits[i];
                        let transaction = _item._source.block_data.trx_id;
                        trxs.add(transaction);
                        let operationType = _item._source.operation_type;
                        let operationContent = _item._source.operation_history.op_object;
                        if (!operationContent) {
                            try {
                                operationContent = JSON.parse(_item._source.operation_history.op)[1]
                            } catch (err) {
                                // nothing
                            }
                        }
                        let esID =  _item._id;
                        newtoadd.push([operationType, operationContent, esID]);
                    }
                    this.txs=trxs.size;

                    document.getElementById('txNumber').classList.remove("changed");
                    document.getElementById('opNumber').classList.remove("changed");
                    void document.getElementById('txNumber').offsetWidth;
                    void document.getElementById('opNumber').offsetWidth;
                    document.getElementById('txNumber').classList.add("changed");
                    document.getElementById('opNumber').classList.add("changed");

                    this.toadd=this.toadd.concat(newtoadd);
                    clearInterval(this.interval);
                    this.interval = setInterval(() => {
                        if (this.toadd.length > 0) {
                            this.operations.push(this.toadd.pop());
                            if (this.operations.length>100) {
                                this.operations.shift();
                            }
                            if (!this.stopped) {
                                this.displayops=this.operations.slice();
                            }
                        }
                    }, 3000 / this.ops);

                    if (!blockFromNode) this.blockNo = block[0][0].head_block_number-2;
                    document.getElementById('blockNumber').classList.remove("changed");
                    void document.getElementById('blockNumber').offsetWidth;
                    document.getElementById('blockNumber').classList.add("changed");
                    let response=details.responses[1];

                    this.txs24total = (
                        1 * response.aggregations["2"].buckets[0]["1"].value
                    ).toLocaleString();
                    let _new = (
                        (response.aggregations["2"].buckets[0]["1"].value * 1) /
                        86400
                    ).toFixed(2);
                    let tx24Changed = !(this.txs24 === _new);
                    this.txs24 = _new;
                    let resOps=details.responses[2];

                    this.ops24total = (
                        1 * resOps.aggregations["3"].buckets[0].doc_count
                    ).toLocaleString();
                    _new = (
                        (resOps.aggregations["3"].buckets[0].doc_count * 1) /
                        86400
                    ).toFixed(2);
                    let ops24Changed = !(this.ops24 === _new);
                    this.ops24 = _new;
                    let resUni=details.responses[3];

                    if (tx24Changed) {
                        document.getElementById('tx24Number').classList.remove("changed");
                        void document.getElementById('tx24Number').offsetWidth;
                        document.getElementById('tx24Number').classList.add("changed");
                    }
                    if (ops24Changed) {
                        document.getElementById('op24Number').classList.remove("changed");
                        void document.getElementById('op24Number').offsetWidth;
                        document.getElementById('op24Number').classList.add("changed");
                    }

                    _new = (
                        1 * resUni.aggregations["4"].buckets[0]["1"].value
                    ).toLocaleString();
                    let uniqueNew = !(_new === this.unique);
                    this.unique = _new;
                    if (uniqueNew) {
                        document.getElementById('uniqueNumber').classList.remove("changed");
                        void document.getElementById('uniqueNumber').offsetWidth;
                        document.getElementById('uniqueNumber').classList.add("changed");
                    }
                }
            });
        },
        methods: {
            toggleUpdates: function() {
                if (this.stopped) {
                    this.stopped=false;
                }else{
                    this.stopped=true;
                }
            },
            getDataFromES: function(blockno, blockFromNode) {

                return this.client.msearch({
                    body: [
                        {},
                        {
                            query: {
                                bool: {
                                    filter: {
                                        term: {
                                            "block_data.block_num": blockno
                                        }
                                    }
                                }
                            },
                            size: 10000
                        },
                        {},
                        {
                            aggs: {
                                "2": {
                                    date_range: {
                                        field: "block_data.block_time",
                                        ranges: [
                                            {
                                                from: "now-24h",
                                                to: "now"
                                            }
                                        ]
                                    },
                                    aggs: {
                                        "1": {
                                            cardinality: {
                                                field: "block_data.trx_id.keyword"
                                            }
                                        }
                                    }
                                }
                            },
                            size: 0,
                            _source: false,
                            stored_fields: ["*"],
                            script_fields: {},
                            query: {
                                bool: {
                                    must: [
                                        {
                                            match_all: {}
                                        },
                                        {
                                            range: {
                                                "block_data.block_time": {
                                                    gte: Date.now()-(24000*3600),
                                                    lte: Date.now(),
                                                    format: "epoch_millis"
                                                }
                                            }
                                        }
                                    ]
                                }
                            }
                        },
                        {},
                        {
                            aggs: {
                                "3": {
                                    date_range: {
                                        field: "block_data.block_time",
                                        ranges: [
                                            {
                                                from: "now-24h",
                                                to: "now"
                                            }
                                        ]
                                    }
                                }
                            },
                            size: 0,
                            _source: {
                                excludes: []
                            },
                            stored_fields: ["*"],
                            script_fields: {},
                            query: {
                                bool: {
                                    must: [
                                        {
                                            match_all: {}
                                        },
                                        {
                                            range: {
                                                "block_data.block_time": {
                                                    gte: Date.now()-(24000*3600),
                                                    lte: Date.now(),
                                                    format: "epoch_millis"
                                                }
                                            }
                                        }
                                    ],
                                    filter: [],
                                    should: [],
                                    must_not: []
                                }
                            }
                        },
                        {},
                        {
                            aggs: {
                                "4": {
                                    date_range: {
                                        field: "block_data.block_time",
                                        ranges: [
                                            {
                                                from: "now-24h",
                                                to: "now"
                                            }
                                        ]
                                    },
                                    aggs: {
                                        "1": {
                                            cardinality: {
                                                field: "account_history.account.keyword"
                                            }
                                        }
                                    }
                                }
                            },
                            size: 0,
                            _source: {
                                excludes: []
                            },
                            stored_fields: ["*"],
                            script_fields: {},
                            query: {
                                bool: {
                                    must: [
                                        {
                                            match_all: {}
                                        },
                                        {
                                            range: {
                                                "block_data.block_time": {
                                                    gte: Date.now()-(24000*3600),
                                                    lte: Date.now(),
                                                    format: "epoch_millis"
                                                }
                                            }
                                        }
                                    ],
                                    filter: [],
                                    should: [],
                                    must_not: []
                                }
                            }
                        }
                    ]
                });
            },
            operationReadableContent: function(opType) {
                return opType;
            },
            operationReadable: function(opType) {
                var name;
                var color;
                if (opType === 0) {
                    name = "TRANSFER";
                    color = "81CA80";
                } else if (opType === 1) {
                    name = "LIMIT ORDER CREATE";
                    color = "6BBCD7";
                } else if (opType === 2) {
                    name = "LIMIT ORDER CANCEL";
                    color = "E9C842";
                } else if (opType === 3) {
                    name = "CALL ORDER UPDATE";
                    color = "E96562";
                } else if (opType === 4) {
                    name = "FILL ORDER (VIRTUAL)";
                    color = "008000";
                } else if (opType === 5) {
                    name = "ACCOUNT CREATE";
                    color = "CCCCCC";
                } else if (opType === 6) {
                    name = "ACCOUNT UPDATE";
                    color = "FF007F";
                } else if (opType === 7) {
                    name = "ACCOUNT WHIELIST";
                    color = "FB8817";
                } else if (opType === 8) {
                    name = "ACCOUNT UPGRADE";
                    color = "552AFF";
                } else if (opType === 9) {
                    name = "ACCOUNT TRANSFER";
                    color = "AA2AFF";
                } else if (opType === 10) {
                    name = "ASSET CREATE";
                    color = "D400FF";
                } else if (opType === 11) {
                    name = "ASSET UPDATE";
                    color = "0000FF";
                } else if (opType === 12) {
                    name = "ASSET UPDATE BITASSET";
                    color = "AA7FFF";
                } else if (opType === 13) {
                    name = "ASSET UPDATE FEED PRODUCERS";
                    color = "2A7FFF";
                } else if (opType === 14) {
                    name = "ASSET ISSUE";
                    color = "7FAAFF";
                } else if (opType === 15) {
                    name = "ASSET RESERVE";
                    color = "55FF7F";
                } else if (opType === 16) {
                    name = "ASSET FUND FEE POOL";
                    color = "55FF7F";
                } else if (opType === 17) {
                    name = "ASSET SETTLE";
                    color = "FFFFAA";
                } else if (opType === 18) {
                    name = "ASSET GLOBAL SETTLE";
                    color = "FFFF7F";
                } else if (opType === 19) {
                    name = "ASSET PUBLISH FEED";
                    color = "FF2A55";
                } else if (opType === 20) {
                    name = "WITNESS CREATE";
                    color = "FFAA7F";
                } else if (opType === 21) {
                    name = "WITNESS UPDATE";
                    color = "F1AA2A";
                } else if (opType === 22) {
                    name = "PROPOSAL CREATE";
                    color = "FFAA55";
                } else if (opType === 23) {
                    name = "PROPOSAL UPDATE";
                    color = "FF7F55";
                } else if (opType === 24) {
                    name = "PROPOSAL DELETE";
                    color = "FF552A";
                } else if (opType === 25) {
                    name = "WITHDRAW PERMISSION CREATE";
                    color = "FF00AA";
                } else if (opType === 26) {
                    name = "WITHDRAW PERMISSION";
                    color = "FF00FF";
                } else if (opType === 27) {
                    name = "WITHDRAW PERMISSION CLAIM";
                    color = "FF0055";
                } else if (opType === 28) {
                    name = "WITHDRAW PERMISSION DELETE";
                    color = "37B68Cc";
                } else if (opType === 29) {
                    name = "COMMITTEE MEMBER CREATE";
                    color = "37B68C";
                } else if (opType === 30) {
                    name = "COMMITTEE MEMBER UPDATE";
                    color = "6712E7";
                } else if (opType === 31) {
                    name = "COMMITTEE MEMBER UPDATE GLOBAL PARAMETERS";
                    color = "B637B6";
                } else if (opType === 32) {
                    name = "VESTING BALANCE CREATE";
                    color = "A5A5A5";
                } else if (opType === 33) {
                    name = "VESTING BALANCE WITHDRAW";
                    color = "696969";
                } else if (opType === 34) {
                    name = "WORKER CREATE";
                    color = "0F0F0F";
                } else if (opType === 35) {
                    name = "CUSTOM";
                    color = "0DB762";
                } else if (opType === 36) {
                    name = "ASSERT";
                    color = "FFFFFF";
                } else if (opType === 37) {
                    name = "BALANCE CLAIM";
                    color = "939314";
                } else if (opType === 38) {
                    name = "OVERRIDE TRANSFER";
                    color = "8D0DB7";
                } else if (opType === 39) {
                    name = "TRANSFER TO BLIND";
                    color = "C4EFC4";
                } else if (opType === 40) {
                    name = "BLIND TRANSFER";
                    color = "F29DF2";
                } else if (opType === 41) {
                    name = "TRANSFER FROM BLIND";
                    color = "9D9DF2";
                } else if (opType === 42) {
                    name = "ASSET SETTLE CANCEL";
                    color = "4ECEF8";
                } else if (opType === 43) {
                    name = "ASSET CLAIM FEES";
                    color = "F8794E";
                } else if (opType === 44) {
                    name = "FBA DISTRIBUTE";
                    color = "8808B2";
                } else {
                  name = "UNKOWN";
                  color = "8808B2";
                }

                return { name, color };
            }
        }
    };
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
h3 {
  margin: 40px 0 0;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
span {
    display:inline-block;
}
table.operationList  {
  width: calc(100vw - 17px);
}
.operationList  td {
  font-size: 11px;
  padding: 10px;
}
.operationList  td code {
  display: block;
  width: calc(100vw - 210px);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.operationList tr:nth-child(even) {
  background: #666666;
}
.operationList tr:nth-child(odd) {
  background: #999999;
}
.changed {
	-webkit-animation: changed 0.4s;
	        animation: changed 0.4s;
}
/* ----------------------------------------------
 * Generated by Animista on 2018-11-9 10:27:34
 * w: http://animista.net, t: @cssanimista
 * ---------------------------------------------- */

/**
 * ----------------------------------------
 * animation tracking-in-expand-fwd
 * ----------------------------------------
 */
@-webkit-keyframes changed {
  0% {
    -webkit-transform: scale(2,2);
    transform: scale(2,2);
    opacity: 0;
  }
  100% {
    -webkit-transform: scale(1,1);
    transform: scale(1,1);
    opacity: 1;
  }
}
@keyframes changed {
  0% {
    -webkit-transform: scale(2,2);
    transform: scale(2,2);
    opacity: 0;
  }
  100% {
    -webkit-transform: scale(1,1);
    transform: scale(1,1);
    opacity: 1;
  }
}
.top {
    display:flex;
  align-items: center;
  justify-content: center;

}
.numbers {
    width:calc(50vw - 17px);
    font-size:1.3vw;
    line-height:1.7vw;
    color: #00a9e0;
    font-weight:normal;
  align-items: center;
  justify-content: center;
}
.numbers td {
    padding-left:3px;
    padding-right:3px;
}
.numbers span {
    font-size:1.5vw;
    line-height:1.7vw;
    color:white;
    font-weight:bold;
}
.logo {
    width:50vw;
    padding:0 20vw 0 0vw;
}
.logo img {
    width:100%;
}

</style>
